:root {
  --tux-dark: #24292E;
  --tux-light: #CBD2DA;
  --tux-red: #D72A1A;
  --tux-orange: #D77A1A;
  --tux-yellow: #B9B927;
  --tux-lime: #6FC618;
  --tux-green: #17BE17;
  --tux-teal: #18C36D;
  --tux-cyan: #15ACAC;
  --tux-ocean: #1A79D7;
  --tux-blue: #3737E7;
  --tux-purple: #8A21F4;
  --tux-magenta: #C318C3;
  --tux-rosa: #E21B7F;
}

body {
  font-style: normal;
  color: #CBD2DA;
  background-color: #24292E;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  min-height: 100vh;
  overflow-x: hidden;
}

header {
  background-color: #16191C;
  display: flex;
  flex-direction: row;
  justify-content: end;
  align-items: center;
  position: fixed;
  top: 0;
  min-width: 100vw;
  height: 4.1rem;
  margin: 0;
  padding-right: 1em;
  z-index: 1000;
}

* {
  margin: 0;
  padding: 0;
